      "/2014/05/27/image-processing-cli-using-imagemagick-montage": {
        "meta": {
          "layout": "post",
          "comments": true,
          "title": "Creating images quickly using montage from imagemagick",
          "date": 1401174060000,
          "original-url": "http://blog.bguiz.com/post/86931632540/image-processing-cli-using-imagemagick-montage",
          "dateurls-override": "/post/86931632540/image-processing-cli-using-imagemagick-montage",
          "tags": [
            "imagemagick",
            "gimpfu",
            "gimp",
            "montage",
            "cli",
            "image",
            "image manipulation"
          ],
          "source": "src/documents/tumblrposts/2014-05-27-image-processing-cli-using-imagemagick-montage.html",
          "url": "/2014/05/27/image-processing-cli-using-imagemagick-montage",
          "urlAliases": [
            "/post/86931632540/image-processing-cli-using-imagemagick-montage"
          ]
        },
        "body": "\n<p>I was looking for a way to combine images together programmatically, and spent some time dabbling around in <a href=\"http://www.gimp.org/docs/python/\" target=\"_blank\"><code>gimpfu</code> python land</a> without getting very far at all.</p>\n\n<p>Who would have thought just placing one image next to another could get so hairy?</p>\n\n<p><img src=\"https://31.media.tumblr.com/2c27307056c3243b3f4ba3ce20cc547b/tumblr_inline_n66mmnEQas1rer3hy.png\" alt=\"This image was created using a single command line\"/></p>\n\n<p>What if I said that it was possible to create the above image from three images using a single command line? Read on!</p>\n\n<p>I had one of those &#8220;Aha!&#8221; moments of discovery when I chanced upon this article: <a href=\"http://www.imagemagick.org/Usage/montage/\" target=\"_blank\">Imagemagick montage usage</a>.</p>\n\n<p><code>montage -geometry 200X200+2+1 foo.png bar.png foobar.png</code></p>\n\n<p>No script involved, just a single command line; and that resizes both <code>foo.png</code> and <code>bar.png</code> to 200 by 200 pixels, adds some default spacing between them (two pixels horizontal, one pixel vertical), and saves the output to <code>foobar.png</code>.</p>\n\n<p>If you want the background to be transparent, and to add a drop shadow, like I did, it is as simple as:</p>\n\n<p><code>montage -shadow -background None -geometry 200X200+2+1 foo.png bar.png foobar.png</code></p>\n\n<p>You can even force the images to \"tile\" in a single horizontal row, like so:</p>\n\n<p><code>montage -shadow -background None -tile x1 -geometry 200x150+5+5 foo.png bar.png foobar.png</code></p>\n\n<p>... or a single vertical row, like so:</p>\n\n<p><code>montage -shadow -background None -tile 1x -geometry 200x150+5+5 foo.png bar.png foobar.png</code></p>\n\n<p>That is all that I needed for what I was doing, but this article explores quite a few other nifty things that the <code>montage</code> tool does - check out the polaroid filter:</p>\n\n<p><img src=\"http://www.imagemagick.org/Usage/montage/polaroid_overlap.jpg\" alt=\"Imagemagick montage with polaroid filter\"/></p>\n"
      },
      "/2014/04/10/modernizr-command-line-interface": {
        "meta": {
          "layout": "post",
          "comments": true,
          "title": "Modernizr Command Line Interface",
          "date": 1397113260000,
          "original-url": "http://blog.bguiz.com/post/82223943673/modernizr-command-line-interface",
          "dateurls-override": "/post/82223943673/modernizr-command-line-interface",
          "tags": [
            "modernizr",
            "nodejs",
            "cli",
            "blog"
          ],
          "source": "src/documents/tumblrposts/2014-04-10-modernizr-command-line-interface.html",
          "url": "/2014/04/10/modernizr-command-line-interface",
          "urlAliases": [
            "/post/82223943673/modernizr-command-line-interface"
          ]
        },
        "body": "\n<p>Modernizr is an an excellent library used for client-side feature detection.</p>\n\n<p><img src=\"http://modernizr.com/i/img/logo-x12.png\" alt=\"Modernizr\"/></p>\n\n<p>The full set of features detected can be obtained using its <a href=\"http://modernizr.com/downloads/modernizr-latest.js\" target=\"_blank\">development version</a>,\nhowever, including this entire file is not ideal in production -\nwhy make users download all the extra code for feature detection tests that will not ever be used?</p>\n\n<p>Thankfully, Modernizr has a nifty <a href=\"http://modernizr.com/download/\" target=\"_blank\">download feature</a>,\nwhich lets you fill in a form to select which tests you want,\nand then it generates a customised file for you to download.</p>\n\n<h2>How about a CLI?</h2>\n\n<p><img src=\"http://modernizr.com/i/img/logo-x12.png\" alt=\"Modernizr\" title=\"Modernizr\"/> +\n<img src=\"http://upload.wikimedia.org/wikipedia/commons/thumb/8/86/Blank-extended-keyboard.svg/300px-Blank-extended-keyboard.svg.png\" alt=\"Command Line\" title=\"COmmand Line\"/> =&#160;?</p>\n\n<p>That is great, however, what if you want to use Modernizr to generate\ncustom files on the command line?</p>\n\n<p>I came across <a href=\"https://github.com/Modernizr/Modernizr/issues/287\" target=\"_blank\">this closed ticket</a> raised against Modernizr,\nand that helped somewhat, as I found a way to generate custom files on the command line,\nbut the command I had to issue was extremely convoluted.</p>\n\n<p>Seeking an easier way, <a href=\"https://gist.github.com/bguiz/9926259\" target=\"_blank\">I came up with this</a>,\nwhich was a huge improvement in usability.</p>\n\n<p>Now it is even easier:</p>\n\n<pre><code>    npm install -g modernizr-cli\n    modernizr custom.json\n</code></pre>\n\n<p>(Where <code>custom.json</code> should conform to <a href=\"https://github.com/Modernizr/Modernizr/blob/master/lib/config-all.json\" target=\"_blank\">this format</a>)</p>\n\n<p>Check it out:\n<a href=\"https://github.com/bguiz/modernizr-cli\" target=\"_blank\">modernizr-cli on github</a> |\n<a href=\"https://www.npmjs.org/package/modernizr-cli\" target=\"_blank\">modernizr-cli on npm</a></p>"
      },
