          "title": "A sneak peek from my talk about qryq at OSDC 2013",
          "date": 1382265360000,
          "original-url": "http://blog.bguiz.com/post/64567226287/a-sneak-peek-from-my-talk-about-qryq-at-osdc-2013",
          "dateurls-override": "/post/64567226287/a-sneak-peek-from-my-talk-about-qryq-at-osdc-2013",
